import React from 'react'
import './comp-styles.css'


export default function Entry({id,task,del}) {

    var deleteHandler = ()=>{
        del(task);
    }
    
    return (
        <div className="entry">
            <p>{(id+1).toString()+") "+task}</p>
            <button onClick={deleteHandler}> X </button>
        </div>
    )
}
